<style>
  .order-id {
    cursor: pointer;
    color: #2077FF;
  }
  .order-id:hover {
    color: #409EFF;
  }
</style>

<template>
  <div>
    <div class="search-form-wraper">
      <el-button type="text" @click="goHome"><i class="fa fa-home" aria-hidden="true"></i>返回首页</el-button>
      <el-button type="text" @click="goOrderMgt" style="margin-left:30px;"><i class="fa fa-list-alt" aria-hidden="true"></i>返回订单管理</el-button>
      <el-button v-if="$route.query.from === 'areaSum'" type="text" @click="goAreaSumMgt" style="margin-left:30px;" icon="el-icon-notebook-2">返回区域商品汇总</el-button>
      <el-button v-if="$route.query.from === 'storeSum'" type="text" @click="goStoreSumMgt" style="margin-left:30px;" icon="el-icon-notebook-2">返回门店商品汇总</el-button>
      <el-button v-if="$route.query.from === 'solitaire'" type="text" @click="goSolitairePage" style="margin-left:30px;" icon="el-icon-notebook-2">返回群接龙查询</el-button>
    </div>

    <!-- 搜索结果 -->
    <div class="table-wraper order-table">
      <el-table
        class="fixed-height"
        v-loading="loading"
        :data="tableData"
        empty-text="没有查询到您想要的数据"
        style="width:100%;"
        size="small">
        <el-table-column min-width="120px" prop="platformName" label="订单来源"></el-table-column>
        <el-table-column min-width="200px" prop="orderId" label="订单号">
          <template slot-scope="scope" size="small">
            <p class="order-id">
              <el-tag size="mini" v-if="scope.row.parentOrderNo">子单</el-tag>
              <router-link :to="scope.row.redirectUrl" target="_blank">{{scope.row.orderId}}</router-link>
            </p>
          </template>
        </el-table-column>
        <el-table-column min-width="86px" prop="orderTypeName" label="订单类型"></el-table-column>
        <el-table-column min-width="160px" prop="erpStoreName" label="所属门店"></el-table-column>
        <el-table-column min-width="130px" label="所属团长店">
          <template slot-scope="scope" size="small">
            <p v-if="scope.row.storeLeaderId">{{scope.row.storeLeaderId}}_{{scope.row.virtualStoreName}}_{{scope.row.userName}}</p>
            <p v-else>-</p>
          </template>
        </el-table-column>
        <el-table-column min-width="100px" prop="rName" label="收货人"></el-table-column>
        <el-table-column min-width="120px" prop="rTel" label="收货手机"></el-table-column>
        <el-table-column min-width="86px" prop="orderTotalAmount" label="订单金额" :formatter="moneyToFixed"></el-table-column>
        <el-table-column min-width="86px" prop="realPaidAmount" label="实付金额" :formatter="moneyToFixed"></el-table-column>
        <el-table-column min-width="86px" prop="deliveryAmount" label="配送费" :formatter="moneyToFixed"></el-table-column>
        <el-table-column min-width="86px" prop="orderStatusName" label="订单状态"></el-table-column>
        <el-table-column min-width="130px" prop="syncErpStatusName" label="同步ERP状态"></el-table-column>
        <el-table-column min-width="176px" prop="orderCreateTime" label="下单时间" :formatter="timeFormatter"></el-table-column>
        <el-table-column min-width="176px" prop="receiveTime" label="要求送达/自提时间" :formatter="timeFormatter"></el-table-column>
      </el-table>
      <!-- 通过订单编号或者手机号查询分页条 -->
      <el-pagination
        v-show="tableData.length>0"
        class="tc mt20"
        background
        @size-change="changeSizeBP"
        @current-change="changeCurrentBP"
        :current-page.sync="currentPageByPhoneNum"
        :page-sizes="[20, 50, 100, 150, 200]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalCount">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import Storage from '@/utils/localStorage'
  import * as utils from '@/utils/utils'
  const orderMgtUrl = {
    // 订单查询接口
    orderQuery: '/plateform/a/order/orderQuery'
  }
  export default {
    data() {
      return {
        loading: false,
        // 手机号/订单号查询用户输入内容
        comonOption: '',
        // 通过手机号/订单号查询
        currentPageByPhoneNum: 1,
        pageSizeByPhoneNum: 20,
        totalCount: 0,
        tableData: []
      }
    },
    created() {
      // 获取页面路由参数
      this.comonOption = this.$store.state.orders.buyerPhone
      if(this.comonOption && this.comonOption != "" || this.$route.query.from === 'areaSum' || this.$route.query.from === 'storeSum' || this.$route.query.from === 'solitaire') {
        this.getOrderMesgByOpt(true)
      }
    },
    computed: {
      getBuyerPhone() {
        let buyerPhoneVal = this.$store.state.orders.buyerPhone
        return buyerPhoneVal
      }
    },
    watch: {
      getBuyerPhone() {
        this.comonOption = this.$store.state.orders.buyerPhone
      },
      comonOption() {
        if(this.comonOption && this.comonOption != "") {
          this.getOrderMesgByOpt()
        }
      }
    },
    methods: {
      /**
       * [changeSizeBP description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSizeBP (value) {
        this.pageSizeByPhoneNum = value
        this.getOrderMesgByOpt()
      },
      /**
       * [changeCurrentBP 点击分页条]
       * @param    {[type]}                 value [当前页]
       * @return   {[type]}                       [description]
       */
      changeCurrentBP (value) {
        this.currentPageByPhoneNum = value
        this.getOrderMesgByOpt()
      },
      /**
       * [getOrderMesgByOpt 通过手机号或者订单编号进行搜索]
       * @return {[type]} [description]
       */
      getOrderMesgByOpt(_param) {
        this.isbySearchBtn = false
        this.loading = true
        if(_param) {
          this.currentPageByPhoneNum = 1
        }

        let query = this.$route.query
        let params = {
          DIFFCITY: true,
          regionCode: Storage.get('regionCode') || ''
        }
        let searchContentMsg = Storage.get("searchContent") ? Storage.get("searchContent").searchContent : {}
        if(query.from === 'areaSum' || query.from === 'storeSum') {
          if(query.from === 'storeSum') {
            params.erpStoreNo = query.storeNo
          }else {
            params.erpStoreNo = null
          }
          if(searchContentMsg) {
            if(searchContentMsg.pickUpTime) {
              params.pickUpTime = searchContentMsg.pickUpTime
            }
            if(searchContentMsg.platformId === 5) {
              params.platformId = 50
            }else if(searchContentMsg.platformId === 6){
              params.platformId = 60
            }
            if(searchContentMsg.businessType) {
              params.businessType = searchContentMsg.businessType
            }
            if(searchContentMsg.startPayTime) {
              params.beginTime = searchContentMsg.startPayTime
            }
            if(searchContentMsg.endPayTime) {
              params.endTime = searchContentMsg.endPayTime
            }
          }
          if(query.storeLeaderId) {
            params.storeLeaderId = query.storeLeaderId
          }else {
            params.storeLeaderId = null
          }
          params.searchType = query.searchType
          params.searchValue = query.searchValue
          params.buyerPhone = null
        }else if(query.from === 'solitaire') {
          params.solitaireId = query.solitaireId
        }else {
          params = {
            buyerPhone: this.comonOption, 
            DIFFCITY: true
          }
        }
        this.$http.post(orderMgtUrl.orderQuery, { pageNum: this.currentPageByPhoneNum, pageSize: this.pageSizeByPhoneNum, ...params }).then(res => {
          this.loading = false
          if(res.code === 0) {
            if(res.data && res.data.result && res.data.result.length) {
              res.data.result.forEach(item => {
                item.redirectUrl = `/order_mgt/detail/${item.orderId}`
              })
              this.tableData = res.data.result
            }else {
              this.tableData = []
            }

            if(this.currentPageByPhoneNum == 1) {
              this.totalCount = res.data.total
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })

      },
      /**
       * [goBack 返回首页]
       * @return {[type]} [description]
       */
      goHome() {
        this.$router.push("/index")
      },
      /**
       * [goBack 返回订单管理]
       * @return {[type]} [description]
       */
      goOrderMgt() {
        let query = this.$route.query
        if(query.from === 'areaSum' || query.from === 'storeSum' || query.from === 'solitaire') {
          Storage.remove('searchContent')
        }
        this.$router.push("/order_mgt")
      },
      goAreaSumMgt() {
        this.$router.push("/arear_pgs")
      },
      goStoreSumMgt() {
        this.$router.push("/store_pgs")
      },
      /**
       * [timeFormatter 时间格式化]
       * @param  {[type]} row       [description]
       * @param  {[type]} column    [description]
       * @param  {[type]} cellValue [description]
       * @return {[type]}           [description]
       */
      timeFormatter(row, column, cellValue) {
        return cellValue && cellValue != "" ? utils.timestampToTimeYMDHM(new Date(cellValue).getTime()) : ''
      },
      /**
       * [moneyToFixed 金额格格式化]
       * @param  {[type]} row       [description]
       * @param  {[type]} column    [description]
       * @param  {[type]} cellValue [description]
       * @return {[type]}           [description]
       */
      moneyToFixed(row, column, cellValue) {
        return cellValue && cellValue != "" ? parseFloat(cellValue).toFixed(2) : '0.00'
      },
      goSolitairePage() {
        this.$router.push("/solitaire_order")
      }
    },
    /**
     * [beforeRouteLeave 页面离开]
     * @param  {[type]}   to   [description]
     * @param  {[type]}   from [description]
     * @param  {Function} next [description]
     * @return {[type]}        [description]
     */
     beforeRouteLeave (to, from, next){
      to.meta.keepAlive = true;
      next();
    }

  }
</script>


